import { createGlobalStyle } from "styled-components";

export const GlobalStyle = createGlobalStyle`
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-family: 'Nunito Sans', sans-serif;
        margin-block-start: 0;
        margin-block-end: 0;
    }

    body {
        height: 130vh;
    }
`;
